* {
    box-sizing: border-box;
    font-family: 'Courier New', Courier, monospace;

}

.main_div {
    display: flex;
    flex-direction: row;

}

.primary_header {
    padding-left: 300px;
}

.secondary_header {
    padding-left: 200px;
}

.comment_div {
   display: flex;
   flex-direction: column;
    height: 200px;
    background-color: #FFEB;
    box-shadow: 1px 1px 1px ;
    border-radius: 5px 5px;
}

.comment_div:hover {
    transform: scaleZ(2);
}



.comment_div_image {
    height: 80px;
    padding-top: 20px;
    padding-left: 20px;
}

.dp {
    border-radius: 100%;
}


.comment_div_icons {
    
    display: flex;
    align-items: end;
    align-content: space-around;
    height: 120px;
}

.comment_icon {
    padding: 5px;
    padding-left: 200px;
}

.comment {
 
    padding-left: 250px;
    padding-bottom: 0px;
}

.icon_button {
    padding: 2px;
    border: none;
}

.icon_button:hover {
    transform: translate3d(5, 5, 5);
}

.main_header {
    display: flex;
    flex-direction: row;
}

.icon {
    padding-left: 0px;
}

.icon_div {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    padding: 5px;
}
.main_nav {
    position: fixed;
}

.sidebar {
    position: relative;
    height: 1080px;
    width: 300px;
    padding: 20px;
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    cursor: pointer;
    background-color: rgb(158, 185, 176);
}

.logout {
    margin-bottom: 0px;
}

.none {
    display: none;
}

.block {
    display: block;
}

.searchfield_div {
    display: flex;
    align-items: baseline;
    border-radius: 10px; 
    
}

input { 
    border-radius: 10px 0px 0px 10px;
    width: 100%;
    font-size: medium;
    box-shadow: 2px 2px 3px;
}

#my-button {
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    box-shadow: 2px 2px 3px;
}

@keyframes fade-In {
    from {
        background-color: bisque;
    }

    to {
        background-color: gainsboro;
    }
}

.main_header {
    background-color: bisque;
    animation-name: fade-In;
    animation-duration: 4sec;
    animation-timing-function: ease-in;
    animation-iteration-count: 2;
}


